body {
    font-family: "Jost", sans-serif;
    background-color: #2f2f2f;
    color: #fff;
}

footer {
    background-color: #1a1a1a;
    margin-top: 3rem;
}

footer p {
    margin: 0;
    padding: 0.25rem;
    text-align: center;
    font-weight: 600;
}

@media (max-width: 575.98px) {
    footer p {
        font-weight: 500;
    }

    footer {
        margin-top: 0rem;
    }
}